    display: grid;     
    margin: auto;
    width: fit-content;
    }

    table{
        width: auto;
    }

    .addBtn {
        width:auto;
        justify-content: center;
    }

    .activeAddBtn {
        margin-top: 10px;
        font-size: 15px;
        padding: 13px 25px;
    }

    .alertComp {
        display:flex;
        justify-content: center;
        margin-bottom: 25px;
    }

    .Inputs{
        margin: 1rem 0;
        border: 1px solid grey
        
    }

    .titleInput{
